<template>
    <div class="container mt-5" id="home">
      <transition name="fade" mode="in-out">
      <div  v-if="theme"
            class="card border-light bg-dark text-white my-auto text-center mt-5 p-5">
          <div  class="card-header p-4 lead"
                v-bind:title="wishTitle"
                v-html="wish">
          </div>
          <div class="card-body row">
          <h5 class="display-4 card-title col-md-9">
            {{ welcome }}
          </h5>
          <button class="btn btn-outline-light mt-4 col-md-3 align-self-center"
                  @click="theme = false">
                  Change Theme
          </button>
        </div>
      </div>
      <div  v-else
            class="card border-light bg-dark text-white my-auto text-center mt-5 p-5 floor">
          <div  class="card-header p-4 lead"
                v-bind:title="wishTitle"
                v-html="wish">
          </div>
          <div class="card-body row">
          <h5 class="display-4 card-title col-md-9">
            {{ welcome }}
          </h5>
          <button class="btn btn-outline-light mt-4 col-md-3 align-self-center"
                  @click="theme = true">
                  Change Theme
          </button>
        </div>
      </div>
    </transition>
    </div>
</template>

<script type="text/javascript">
  export default {
    data(){
      return {
        welcome: `Welcome to my page`,
        wish: `I wish you a nice stay, <strong :title="wishTitle"> stranger</strong>`,
        wishTitle: `I quite always forget the people I meet. I'm sorry if we met before.`,
        theme: false
      }
    },
    beforeRouteLeave(to, from, next){
      console.log('HI THERE');
      next();
    }
  }
</script>

<style scoped>
  h5, .card-header, button.btn {
    font-variant: small-caps !important;
  }

  button.btn {
    height: 30% !important;
  }

  .fade-enter {
    opacity: 0;
    transform: translateX(-100px);
  }

  .fade-enter-active {
    transition: all 1800ms ease;
  }

  .fade-leave-active {
    opacity: 0;
    transition: all 800ms;
  }

  .floor {
    background-image: url('/src/assets/floor.jpg');
    background-position: top;
    background-size: cover;
  }

</style>



